<template>
  <div>
    <div class="content">
      <div class="num">{{this.currentOrder}}/8</div>
      <div class="title-container">
        <div class="title">一个家庭有两个小孩，其中有一个是女孩，问另一个也是女孩的概率？</div>
      </div>
      <div class="options">
        <div class="option-item" @click="handleRightKey">
          <span>A.</span>
          <span>1/3</span>
        </div>
        <div class="option-item" @click="handleWrongKey">
          <span>B.</span>
          <span>1/2</span>
        </div>
        <div class="option-item" @click="handleWrongKey">
          <span>C.</span>
          <span>1/4</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapMutations, mapGetters } from "vuex";
import getRandomSerial from "../util/randnum.js";
import Router from "../router";

export default {
  data() {
    return {
      calcScore: 1,
      currentOrder: "",
      currentPageNum: 8,
      nextPage: ""
    };
  },
  mounted() {
    this.setNextPage();
  },
  methods: {
    handleRightKey() {
      this.setCalcCount(this.calcScore);
      Router.replace(this.nextPage);
    },
    handleWrongKey() {
      Router.replace(this.nextPage);
    },
    setNextPage() {
      this.setFinished(this.currentPageNum);
      this.currentOrder = this.finished.length;
      const nextPageNum = getRandomSerial(this.finished);
      this.nextPage = `/${nextPageNum}`;
    },
    ...mapMutations({
      setCalcCount: "SET_CALC_COUNT",
      setFinished: "SET_FINISHED"
    })
  },
  computed: {
    ...mapGetters(["calcCount", "finished"])
  }
};
</script>
<style lang="scss" scoped>
.content {
  margin: 0;
  height: 100%;
  padding: 0;
  text-align: center;
  font-size: 1rem;
  background: {
    image: url(https://static.shiyanlou.com/img/test_question/question-white-bg.png);
    repeat: no-repeat;
    size: cover;
  }
  .num {
    height: 3rem;
    line-height: 3rem;
    color: rgb(108, 108, 108);
  }
  .title-container {
    display: flex;
    width: 16rem;
    height: 5rem;
    margin: 0 auto;
    align-items: center;
    .title {
      text-align: left;
      color: #666;
    }
  }
  .options {
    display: flex;
    flex-direction: column;
    .option-item {
      width: 15rem;
      height: 2.2rem;
      margin: 1.1rem auto 0;
      border-radius: 0.6rem;
      background: #315c58;
      span {
        line-height: 1.4rem;
        color: #ccc;
        &:nth-child(1) {
          display: inline-block;
          vertical-align: middle;
        }
        &:nth-child(2) {
          display: inline-block;
          width: 12rem;
          padding: 0.4rem 0;
          text-align: left;
          vertical-align: -0.1rem;
          color: #ccc;
        }
      }
    }
  }
  .logo-container {
    position: absolute;
    width: 6rem;
    height: 3rem;
    bottom: 1rem;
    left: 50%;
    transform: translateX(-50%);
    background: {
      image: url(https://static.shiyanlou.com/img/test_question/logo.png);
      repeat: no-repeat;
      size: contain;
    }
  }
}
</style>


