<template>
  <div>
    <div ref="certifyContainer" class="content">
      <div class="top-container">
        <div class="avatar-img">
          <img v-if="avatar" :src="avatar" />
        </div>
        <div class="level">{{level.nick}}</div>
        <div class="salary">月薪:
          <span>{{level.salary}}</span>
        </div>
        <div class="certify-img"></div>
        <div class="level-star-contain">
          <div class="star-left">
            <span>逻辑能力:</span>
            <span class="star-img" v-for="item in logicArr" :key="item"></span>
          </div>
          <div class="star-right">
            <span>运算能力:</span>
            <span class="star-img" v-for="item in calcArr" :key="item"></span>
          </div>
        </div>

      </div>

      <div class="middle-container">
        <a href="https://mp.weixin.qq.com/s/4qCuHEkxLO6Yedg9-v2X8w" class="programmer-book">
          1024！搞点事情
        </a>
        <div class="middle-btn">
          <a class="repeat" href="/test_question">重新测试</a>
        </div>

      </div>

      <div class="bottom-container">
        <div class="left">
          <div class="title">
            实验楼
          </div>
          <div class="host">shiyanlou.com</div>
          <div class="desc">
            动手做实验，轻松学IT
          </div>
        </div>
        <div class="right">
          <div class="code-img">
            <img src="https://static.shiyanlou.com/img/test_question/restart.png" alt="">
          </div>
          <div class="desc">
            截图分享<br />邀好友一起测试
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import html2canvas from "html2canvas";
import getRandomSerial from "../util/randnum.js";
import Router from "../router";
import getUserInfo from "../util/getUserInfo.js";

export default {
  data() {
    return {
      avatar: "",
      nickName: "",
      nextPage: "",
      level: {},
      calcArr: [],
      logicArr: []
    };
  },
  created() {
    this._getUserInfo();
  },
  mounted() {
    this.setStar();
    this.setSalary();
  },
  methods: {
    _getUserInfo() {
      getUserInfo().then(res => {
        this.avatar = res.headimgurl;
        this.avatar = this.avatar.replace(/thirdwx.qlogo.cn/,'www.shiyanlou.com/wechat_avatar');
        this.nickName = res.nickname;
      });
    },
    setStar() {
      this.calcArr.length = this.calcCount + 1;
      this.logicArr.length = this.logicCount + 1;
    },
    setSalary() {
      const score = this.calcCount + this.logicCount + 2;
      if (0 < score && score <= 4) {
        this.level = {
          salary: "3000+",
          nick: "Bug制造机"
        };
      } else if (4 < score && score <= 6) {
        this.level = {
          salary: "6000+",
          nick: "代码搬运工"
        };
      } else if (6 < score && score <= 8) {
        this.level = {
          salary: "10000+",
          nick: "编程大牛"
        };
      } else {
        this.level = {
          salary: "100000+",
          nick: "骨灰级程序员"
        };
      }
    },
    setCertifyImg() {
      Router.replace({path:'/img',query:{avaurl:this.avatar}});
    }
  },
  computed: {
    ...mapGetters(["finished", "logicCount", "calcCount"])
  }
};
</script>

<style lang="scss" scoped>
.content {
  margin: 0;
  height: 100%;
  padding: 0;
  text-align: center;
  font-size: 1rem;
  z-index: 99;
  .top-container {
    position: relative;
    height: 60%;
    padding-top: 2rem;
    background: {
      image: url(https://static.shiyanlou.com/img/test_question/poster-bg.png);
      repeat: no-repeat;
      size: cover;
      color: rgb(49, 92, 88);
    }
    .avatar-img {
      width: 6rem;
      height: 6rem;
      margin: 0 auto;
      overflow: hidden;
      border: 3px solid #fff;
      border-radius: 50%;
      background: {
        image: url(https://static.shiyanlou.com/img/logo-mobile.png);
        repeat: no-repeat;
        size: 80%;
        position: center;
        color: #fff;
      }
      img {
        width: 100%;
        height: 100%;
      }
    }
    .level {
      margin-top: 0.8rem;
      color: #fff;
      font-size: 1.3rem;
    }
    .salary {
      line-height: 2rem;
      font-size: 0.9rem;
      color: #fff;
    }
    .certify-img {
      position: absolute;
      width: 6rem;
      height: 6rem;
      right: 1rem;
      top: 42%;
      background: {
        image: url(https://static.shiyanlou.com/img/test_question/result-certify2.png);
        repeat: no-repeat;
        size: contain;
      }
    }
    .level-star-contain {
      width: 10rem;
      margin: 0.6rem auto 0;
      text-align: left;
      font-size: 0.9rem;
      color: #fff;
      .star-left,
      .star-right {
        .star-img {
          display: inline-block;
          width: 1rem;
          height: 1rem;
          vertical-align: -0.2rem;
          background: {
            image: url(https://static.shiyanlou.com/img/test_question/star.svg);
            repeat: no-repeat;
            size: contain;
          }
        }
      }
    }
    .share {
      position: absolute;
      left: 50%;
      bottom: 1rem;
      font-size: 0.7rem;
      color: #aaa;
      transform: translateX(-50%);
      text-decoration: none;
    }
  }
  .middle-container {
    .middle-btn {
      display: flex;
      justify-content: center;
      margin-top:5px;
      a {
        display: block;
        margin: 10px;
        color: #666;
        font-size: 0.8rem;
      }
    }
    .programmer-book {
      display: inline-block;
      margin-top: 1.5rem;
      padding: 0.3rem 0.6rem;
      border-radius: 0.6rem;
      color: #fff;
      background: rgb(49, 92, 88);
      text-decoration: none;
    }
  }
  .bottom-container {
    margin-top: 1.2rem;
    .left {
      position: absolute;
      width: 8rem;
      left: 0.7rem;
      bottom: 0.8rem;
      text-align: left;
      .title {
        color: rgb(102, 102, 102);
      }
      .host {
        font-size: 0.5rem;
        font-weight: 200;
        color: rgb(102, 102, 102);
      }
      .desc {
        font-size: 0.7rem;
        color: rgb(102, 102, 102);
      }
      .content-text {
        font-size: 0.3rem;
        font-weight: 200;
        color: #ccc;
      }
    }
    .right {
      position: absolute;
      right: 0.7rem;
      bottom: 0.8rem;
      text-align: left;
      .code-img {
        width: 5rem;
        height: 5rem;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .desc {
        padding-left: 0.5rem;
        color: rgb(102, 102, 102);
        font-size: 0.5rem;
      }
    }
  }
}

.content-poster {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: unset;
  background: {
    image: url(https://static.shiyanlou.com/img/test_question/poster-bg.png);
    repeat: no-repeat;
    size: cover;
    color: rgb(49, 92, 88);
  }
  .top-container {
    background: unset;
    .poster-title {
      margin-bottom: 2.6rem;
      margin-top: -0.6rem;
      font-size: 1.2rem;
      color: rgb(8, 194, 147);
      img {
        width: 1.8rem;
      }
    }
    .certify-img {
      top: 66%;
    }
  }
  .bottom-container {
    .left {
      position: absolute;
      left: 0.7rem;
      bottom: 0.8rem;
      .title {
        color: #fff;
      }
      .host {
        color: #fff;
      }
      .desc {
        color: #fff;
      }
    }
    .right {
      position: absolute;
      right: 0.7rem;
      bottom: 0.8rem;
      text-align: center;
      .desc {
        padding: 0;
        color: #fff;
      }
      .code-img {
        width: 5rem;
        height: 5rem;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
</style>



