/*eslint-disable */
import Vue from 'vue'
import Router from 'vue-router'
import Start from '../components/start.vue'
import One from '../components/one.vue'
import Two from '../components/two.vue'
import Three from '../components/three.vue'
import Four from '../components/four.vue'
import Five from '../components/five.vue'
import Six from '../components/six.vue'
import Seven from '../components/seven.vue'
import Eight from '../components/eight.vue'
import Certificate from '../components/certificate.vue'
import Certimg from '../components/certificateImg.vue'
import Devsecret from '../components/devSecret.vue'

Vue.use(Router)
export default new Router({
    routes: [
        {
            path: '/',
            redirect: '/start',
        },
        {
            path: '/start',
            component: Start,
        },
        {
            path: '/1',
            name: 'One',
            component: One,
        },
        {
            path: '/2',
            name: 'Two',
            component: Two,
        },
        {
            path: '/3',
            name: 'Three',
            component: Three,
        },
        {
            path: '/4',
            name: 'Four',
            component: Four,
        },
        {
            path: '/5',
            name: 'Five',
            component: Five,
        },
        {
            path: '/6',
            name: 'Six',
            component: Six,
        },
        {
            path: '/7',
            name: 'Seven',
            component: Seven,
        },
        {
            path: '/8',
            name: 'Eight',
            component: Eight,
        },
        {
            path: '/certificate',
            name: 'Certificate',
            component: Certificate,
        },
        {
            path: '/img',
            name: 'Certimg',
            component: Certimg,
        },
        {
            path: '/devsecret',
            name: 'Devsecret',
            component: Devsecret,
        }
    ]
})



