import * as types from './mutation-types'
/*eslint-disable */
const mutations = {
    [types.SET_CALC_COUNT](state, calcCount) {
        state.calcCount += calcCount
    },
    [types.SET_LOGIC_COUNT](state, logicCount) {
        state.logicCount += logicCount
    },
    [types.SET_FINISHED](state, finishedValue) {
        state.finished.push(finishedValue)
    }
}

export default mutations


