<template>
  <div>
    <div class="dev-secret-container">
      <div class="title">
        编程之巅的直达电梯就在
        <span class="keyword">实验楼</span>
      </div>
      <div class="statistic-img">
        <img src="https://static.shiyanlou.com/img/test_question/statistic.png" alt="">
      </div>
      <div class="desc">关注实验楼 | 开启编程之路</div>
      <div class="introduction-img">
        <img src="https://static.shiyanlou.com/img/test_question/introduction.png" alt="">
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  mounted() {
    this.updateTitle();
  },
  methods: {
    updateTitle() {
      document.title = "程序员养成秘籍";
    }
  }
};
</script>
<style lang="scss" scoped>
.dev-secret-container {
  height: 100%;
  text-align: center;
  background: {
    image: url(https://static.shiyanlou.com/img/test_question/question-white-bg.png);
    repeat: no-repeat;
    size: cover;
    color: #fff;
  }
  .title {
    text-align: center;
    line-height: 5rem;
    font-size: 1.2rem;
    color: rgb(102, 102, 102);
    .keyword {
      color: rgb(0, 156, 109);
    }
  }
  .statistic-img {
    margin-top: 1.6rem;
    text-align: center;
    img {
      width: 85%;
    }
  }
  .desc {
    display: inline-block;
    margin: 1.5rem auto;
    padding: 0.5rem 0.7rem;
    border-radius: 0.5rem;
    font-size: 1rem;
    color: #fff;
    background: rgb(0, 156, 109);
  }
  .introduction-img {
    position: absolute;
    bottom: 0;
    img {
      width: 100%;
    }
  }
}
</style>



