<template>
    <div>
        <div class="content">
            <div class="top-img"></div>
            <div class="middle-img"></div>
            <div class="bottom-img" @click="handleStart">开始测试</div>
        </div>
    </div>
</template>
<script>
import { mapMutations, mapGetters } from "vuex";
import getRandomSerial from "../util/randnum.js";
import Router from "../router";

export default {
  data() {
    return {
      nextPage: ""
    };
  },
  mounted() {
    this.setNextPage();
  },
  methods: {
    handleStart() {
      Router.replace(this.nextPage);
    },
    setNextPage() {
      const nextPageNum = getRandomSerial(this.finished);
      this.nextPage = `/${nextPageNum}`;
    }
  },
  computed: {
    ...mapGetters(["finished"])
  }
};
</script>
<style lang="scss" scoped>
.content {
  margin: 0;
  height: 100%;
  padding: 0;
  text-align: center;
  font-size: 1rem;
  background: {
    image: url(https://static.shiyanlou.com/img/test_question/question-bg.png);
    repeat: no-repeat;
    size: cover;
    color: rgb(49, 92, 88);
  }
  .top-img {
    width: 12rem;
    height: 14rem;
    margin: 4rem auto 0;
    background: {
      image: url(https://static.shiyanlou.com/img/test_question/item-start-top1.png);
      repeat: no-repeat;
      size: contain;
    }
  }
  .middle-img {
    width: 9rem;
    height: 11rem;
    margin-top: -3.6rem;
    background: {
      image: url(https://static.shiyanlou.com/img/test_question/item-start-middle.png);
      repeat: no-repeat;
      size: contain;
    }
  }
  .bottom-img {
    width: 7rem;
    height: 2.6rem;
    margin: -2rem auto 0;
    line-height: 2.6rem;
    color: #fff;
    background: {
      image: url(https://static.shiyanlou.com/img/test_question/item-start-bottom.png);
      repeat: no-repeat;
      size: contain;
    }
  }
  .logo-container {
    position: absolute;
    width: 6rem;
    height: 3rem;
    bottom: 2rem;
    left: 50%;
    transform: translateX(-50%);
    background: {
      image: url(https://static.shiyanlou.com/img/test_question/logo.png);
      repeat: no-repeat;
      size: contain;
    }
  }
}
</style>



