<template>
  <div>
    <div class="content">
      <div class="num">{{this.currentOrder}}/8</div>
      <div class="title-container">
        <div class="title">[多选]下水道的井盖为什么是圆的？</div>
      </div>
      <div class="options">
        <div class="option-item" @click="handleRightAnswer">
          <span>A.</span>
          <span>圆的井盖不容易掉进去</span>
        </div>
        <div class="option-item" @click="handleRightAnswer">
          <span>B.</span>
          <span>圆的井盖容易加工</span>
        </div>
        <div class="option-item" @click="handleRightAnswer">
          <span>C.</span>
          <span>圆的下水道更容易挖</span>
        </div>
        <div class="option-item" @click="handleRightAnswer">
          <span>D.</span>
          <span>圆的井盖更方便使用</span>
        </div>
      </div>
      <div class="next-question" @click="handleRightKey">进入下一题</div>
    </div>
  </div>
</template>
<script>
import { mapMutations, mapGetters } from "vuex";
import getRandomSerial from "../util/randnum.js";
import Router from "../router";

export default {
  data() {
    return {
      logicScore: 0,
      currentOrder: "",
      currentPageNum: 2,
      nextPage: ""
    };
  },
  mounted() {
    this.setNextPage();
  },
  methods: {
    handleRightAnswer(e) {
      this.logicScore += 1;
      e.currentTarget.classList.add("disabled-click");
    },
    handleRightKey() {
      this.logicScore = this.logicScore >= 3 ? 1 : 0;
      this.setLogicCount(this.logicScore);
      Router.replace(this.nextPage);
    },
    handleWrongKey() {
      Router.replace(this.nextPage);
    },
    setNextPage() {
      this.setFinished(this.currentPageNum);
      this.currentOrder = this.finished.length;
      const nextPageNum = getRandomSerial(this.finished);
      this.nextPage = `/${nextPageNum}`;
    },
    ...mapMutations({
      setLogicCount: "SET_LOGIC_COUNT",
      setFinished: "SET_FINISHED"
    })
  },
  computed: {
    ...mapGetters(["logicCount", "finished"])
  }
};
</script>
<style lang="scss" scoped>
.content {
  margin: 0;
  height: 100%;
  padding: 0;
  text-align: center;
  font-size: 1rem;
  background: {
    image: url(https://static.shiyanlou.com/img/test_question/question-white-bg.png);
    repeat: no-repeat;
    size: cover;
  }
  .num {
    height: 3rem;
    line-height: 3rem;
    color: rgb(108, 108, 108);
  }
  .title-container {
    display: flex;
    width: 16rem;
    height: 5rem;
    margin: 0 auto;
    align-items: center;
    .title {
      text-align: left;
      color: #666;
    }
  }
  .options {
    display: flex;
    flex-direction: column;
    .disabled-click {
      background: #888 !important;
      pointer-events: none;
    }
    .option-item {
      width: 15rem;
      height: 2.2rem;
      margin: 1.1rem auto 0;
      border-radius: 0.6rem;
      background: #315c58;
      span {
        line-height: 1.4rem;
        color: #ccc;
        &:nth-child(1) {
          display: inline-block;
          vertical-align: middle;
        }
        &:nth-child(2) {
          display: inline-block;
          width: 12rem;
          padding: 0.4rem 0;
          text-align: left;
          vertical-align: -0.1rem;
          color: #ccc;
        }
      }
    }
  }
  .next-question {
    display: inline-block;
    margin-top: 1.6rem;
    padding: 0.4rem 0.6rem;
    color: #ccc;
    font-size: 0.8rem;
    border-radius: 8px;
    background: #315c58;
  }
  .logo-container {
    position: absolute;
    width: 6rem;
    height: 3rem;
    bottom: 1rem;
    left: 50%;
    transform: translateX(-50%);
    background: {
      image: url(https://static.shiyanlou.com/img/test_question/logo.png);
      repeat: no-repeat;
      size: contain;
    }
  }
}
</style>


