const getRandomSerial = (arr) => {
  if (arr.length === 8) {
    return 'certificate'
  }
  const temp = 1 + Math.round(Math.random() * 7)
  if (!arr.includes(temp)) {
    return temp
  }
  return getRandomSerial(arr)
}

export default getRandomSerial



