import Vue from 'vue'
import Axios from 'axios'
import App from './app.vue'
import router from './router'
 import store from './store'

 Vue.prototype.$axios = Axios

/* eslint-disable no-new */
new Vue({
   el: '#app',
  router,
   store,
   template: '<App/>',
   components: { App },
 })



